    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    text-align: center;
    max-width: 400px;
    margin: 10px;
}

.operation-image {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
}

.operation-name {
    font-size: 22px;
    margin: 10px 0;
}

.operation-category {
    font-size: 18px;
    color: #777;
    margin: 5px 0;
}

.operation-description {
    font-size: 16px;
    color: #333;
    margin: 10px 0;
}

.operation-price {
    font-size: 18px;
    font-weight: bold;
    margin: 10px 0;
}

